const tabsBtn = document.querySelectorAll('.tabs__nav-btn');
const tabsItems = document.querySelectorAll('.works__gallery');

let modal = document.querySelector('.modal');
const modalPlay = document.querySelectorAll('.main-right__video-play');
const modalClose = document.querySelector('.modal-work__close');

tabsBtn.forEach(function(item) {
    item.addEventListener('click', function() {

        let currentBtn = item;
        let tabId = currentBtn.getAttribute('data-tab');
        let currentTab = document.querySelector(tabId);

        
        if ( ! currentBtn.classList.contains('active') ) {
            tabsBtn.forEach(function(item) {
                item.classList.remove('active');
            });
    
    
            tabsItems.forEach(function(item) {
                item.classList.remove('active');
            });
        }
        
      



        currentBtn.classList.add('active');
        currentTab.classList.add('active');
        
    });
})



modalPlay.forEach(function(item) {
    item.addEventListener('click', function() {
        let body = document.body;
        body.classList.add('no-scroll');
        
        modalClose.addEventListener('click', function(){
            modal.classList.remove('active');
            body.classList.remove('no-scroll');
        });
     
        modal.classList.add('active');
    });
})